@import "sass/index";

.table {
  padding: 0;
}
.content {
}

.tableInnerWrapper {
}
.tableBody,
.tableHead {
  & * {
    @media (max-width: 600px) {
      font-size: 0.75rem;
    }
  }
}
.tableHead {
  & * {
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.25);
    letter-spacing: 1px;
    text-align: center;
    line-height: 15px;
  }
}
.tableBody {
  & * {
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.65);
    letter-spacing: 0;
    text-align: center;
    line-height: 16px;
  }

  @media (max-width: $mobileScreen) {
    & * {
      font-weight: 600;
      color: rgba(255, 255, 255, 0.65);
    }
  }
}
.tableCellWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}
.tableCell {
  border-bottom-color: transparent;
  padding: 10px;
  border: none;

  @media (max-width: $mobileScreen) {
    padding: 5px 2px;
  }
}

.profit {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin: 0;
  margin-right: 10px;
}

.profitWin {
  background: $win-green !important;
}
.profitLose {
  background: $lose-red !important;
}
.profitBox {
  border-radius: 14px;
  padding: 2px 16px;
  color: #000000 !important;

  @media (max-width: $mobileScreen) {
    padding: 2px 8px;
  }
}
.coinIcon {
  position: static;
  margin-right: 5px;
  height: 20px;
  width: 20px;
  cursor: default;
  color: black;

  @media (max-width: $mobileScreen) {
    margin-right: 2px;
  }
}
