@import "sass/index";

.paper {
  width: 530px;
  height: max-content;
  background: linear-gradient(
      135deg,
      #3b1871 0%,
      rgba(11, 4, 21, 0.181709) 48.48%,
      rgba(3, 1, 4, 0.0001) 80.79%,
      #000000 100%
    ),
    #0a0d18;
  border-radius: 10px;

  @media (max-width: $mobileScreen) {
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
}
.box {
  overflow: auto;
  margin: 0 20px;

  @media (max-width: 530px) {
    margin: 0 10px;
  }
}
.title {
  font-size: 22px;
  font-weight: bold;
  width: 100%;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: space-between;
  line-height: 27px;
  letter-spacing: 1px;
  padding: 16px 40px 16px 16px;

  color: white;
}

.subTitle {
  position: static;
  color: white;
}
.pagination {
  width: max-content;
}

.closeIcon {
  margin: 0;
  padding: 0;
  width: 16px;
  height: 16px;

  & path {
    stroke: white;
  }
}
