$buttonSize: 30px;

.content {
  height: 40px;
  padding: 5px;
  margin: 0;
  margin-bottom: 10px;
  display: flex;
  justify-content: flex-end;
  align-self: center;
  align-items: center;
  list-style-type: none;
  color: rgba(255, 255, 255, 0.35);
}
.number {
  width: $buttonSize;
  height: $buttonSize;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  overflow: hidden;
  margin: 0 5px;
  cursor: pointer;
}
.disable {
  cursor: default;
  opacity: 0.5;
}

.icon {
  fill: currentColor;
}
